<template>
  <div id="page-user-view">

    <div id="user-data" v-if="getUser">
      <vx-card class="mb-base">
		  	<div class="vx-row">
				<div class="vx-col sm:w-1/2 w-full">
					<h3 class="flex items-end">
						<feather-icon svgClasses="w-6 h-6" icon="UserIcon" class="mr-2" />
						<span class="font-medium text-lg leading-none">Account</span>
					</h3>
					<vs-divider />
					<div class="vx-row">
						<!-- Avatar Col -->
						<div class="vx-col avatar-col">
							<div class="img-container mb-4">
							<img src="/img/avatar-s-11.1a620230.jpg" class="rounded w-full" />
							</div>
						</div>

						<!-- Information - Col 1 -->
						<div class="vx-col flex-1">
							<table>
							<tr>
								<td class="font-semibold">Username</td>
								<td>@{{ getUser.username }}</td>
							</tr>
							<tr>
								<td class="font-semibold">First name</td>
								<td>{{ getDispatcher.profile.firstName }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Last name</td>
								<td>{{ getDispatcher.profile.lastName }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Status</td>
								<td>{{ getUser.archived ? 'Archived' : 'Available' }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Role</td>
								<td> {{ roles.join(", ") }}</td>
							</tr>
							</table>
						</div>
						<!-- /Information - Col 1 -->
					</div>
				</div>
				<div class="vx-col sm:w-1/2 w-full">
					<h3 class="flex items-center">
						<feather-icon svgClasses="w-6 h-6" icon="AlertCircleIcon" class="mr-2" />
						<span class="font-medium text-lg leading-none">Information</span>
					</h3>
					<vs-divider />
					<div class="vx-row">
						<!-- Information - Col 2 -->
						<div class="vx-col flex-1">
							<table>
							<tr>
								<td class="font-semibold">Phone</td>
								<td>{{ getDispatcher.profile.contact.phone }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Email</td>
								<td>{{ getDispatcher.profile.contact.email }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Address</td>
								<td>{{ getDispatcher.profile.contact.address }}</td>
							</tr>
							</table>
						</div>
						<!-- /Information - Col 2 -->
					</div>
				</div>
		  	</div>

        <div class="vx-row">
          <div class="vx-col w-full flex justify-end pt-5">
            <vs-button icon-pack="feather" icon="icon-edit" class="mr-4" :to="{name: 'app-user-edit', params: { userId: $route.params.userId }}" @click.native="$router.push({ name: 'useredit' })">Edit</vs-button>
            <vs-button type="border" color="danger" icon-pack="feather" icon="icon-trash" @click="confirmDeleteRecord">Delete</vs-button>
          </div>
        </div>

      </vx-card>

    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
	data() {
		return {}
	},
	computed: {
		...mapGetters("user", ["getUser", "getDispatcher"]),
		roles () {
			return this.getUser.roles.map(item => item.name)
		}
  	},
    methods: {
		confirmDeleteRecord() {
			this.$vs.dialog({
				type: 'confirm',
				color: 'danger',
				title: `Confirm Delete`,
				text: `You are about to delete "${this.getDispatcher.profile.firstName}"`,
				accept: this.deleteRecord,
				acceptText: "Delete"
			})
    	},
		deleteRecord() {
			this.showDeleteSuccess()
		},
		showDeleteSuccess() {
			this.$vs.notify({
				color: 'success',
				title: 'User Deleted',
				text: 'The selected user was successfully deleted'
			})
		}
  	}
}

</script>

<style lang="scss">
.avatar-col {
  width: 8.5rem;
}

#page-user-view {
  table {
    td {
      vertical-align: top;
      min-width: 140px;
      padding-bottom: .8rem;
      word-break: break-all;
    }

    &:not(.permissions-table) {
      td {
        @media screen and (max-width:370px) {
          display: block;
        }
      }
    }
  }
}

</style>